import style from "./style.module.scss";
import clsx from "clsx";
import {useTranslation} from "react-i18next";
import Obfuscate from 'react-obfuscate';
import PhoneIcon from '@material-ui/icons/Phone';
import EmailIcon from '@material-ui/icons/Email';
import HomeIcon from '@material-ui/icons/Home';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';
import FlightIcon from '@material-ui/icons/Flight';
import {useState} from "react";
import {Fade} from "@material-ui/core";

const PhotoInfo = () => {
    const [showClick, setShowClick] = useState(true);
    const [showNumber, setShowNumber] = useState(false);
    const {t} = useTranslation();

    return <>
        <PhoneIcon/>
        <Fade in={showClick} unmountOnExit={true} onExited={() => setShowNumber(true)}>
            <span onClick={() => setShowClick(false)}>{t("Cliquez pour afficher")}</span>
        </Fade>
        <Fade in={showNumber} unmountOnExit={true} mountOnEnter={true}>
            <span><Obfuscate tel={"+33.6.78.66.30.85"}/></span>
        </Fade>
    </>
}

const ContactInfos = ({className}: { className?: string }) => {
    const {t} = useTranslation();
    return <div className={clsx({
        [style.block]: true,
        [className ?? ""]: className ?? false
    })}>
        <div className={clsx({
            [style.contact]: true,
            [style.phone]: true,
        })}>
            <PhotoInfo/>
        </div>
        <Obfuscate email={"romain@torrente.eu"} className={style.contact} obfuscateChildren={false}>
            <EmailIcon/> <Obfuscate email={"romain@torrente.eu"} element={"span"}/>
        </Obfuscate>
        <div className={style.contact}>
            <HomeIcon/> {t("Région de Lyon, France")}
        </div>
        <a className={style.contact} target={"_blank"} rel={"noreferrer noopener"} href={"https://www.linkedin.com/in/romain-torrente/"}>
            <LinkedInIcon/> romain-torrente
        </a>
        <a className={style.contact} target={"_blank"} rel={"noreferrer noopener"} href={"https://github.com/rtorrente"}>
            <GitHubIcon/> rtorrente
        </a>
        <div className={style.contact}>
            <FlightIcon/> {t("Mobilité internationnale")}
        </div>
    </div>
}

export default ContactInfos;
